*::before,
*::after {
  box-sizing: border-box;
}

:root {
  --border-clr: darkorange;
  --trail-clr: orange;
  --tile-clr: rgb(0,0,0);
}

body {
  background-color: white;

  max-height: 100vh;
  max-width: 600px;
  margin: 0 auto;
}

h1 {
  padding: 0.5rem;
  margin: 0;
}

.tile {
  background-color: var(--tile-clr, black);
  
  border: 1px solid var(--border-clr, darkorange);
  
  margin: 0px;
}

